<script>
import dayjs from 'dayjs'
import { Message } from 'element-ui'
import Fetch from '../../utils/fetch'
import Fetch2 from '../../utils/fetch2'
import Fetch3 from '../../utils/fetch3'

export default {
  data() {
    return {
      loading: false,
      config: {
        status: {
			N:'禁止',
			S:'冻结',
			Y:"正常",
		},
        is_auth: {},
      },
      form: {
        status: 'Y',
      },
	  idCard:{
	  		  card1:'',
	  		  card2:'',
	  },
	  idCardList:[],
	  is_auth:{
	  		  U:{name:'等待审核',yn:'Đợi xét duyệt'},
	  		  Y:{name:'审核通过',yn:'Đã xác thực'},
	  		  N:{name:'未审核',yn:'Chưa xác thực'},
	  		  F:{name:'审核不通过',yn:'Xét duyệt thất bại'},
	  }
    }
  },
  watch: {
    dk_time(val) {
      if (val && val.length) {
        this.form.min_dk_time = dayjs(val[0]).unix()
        this.form.max_dk_time = dayjs(val[1]).unix()
      } else {
        ;(this.form.min_dk_time = ''), (this.form.max_dk_time = '')
      }
    },
  },
  mounted() {
    const id = this.$router.history.current.params.id
    this.loading = true
    Promise.all([
      Fetch('/user/user/config').then(r => {
		  this.config = r.data
		  this.config.is_auth['U'] = '等待审核';
		  this.config.is_auth['Y'] = '审核通过';
		  this.config.is_auth['N'] = '未审核';
		  this.config.is_auth['F'] = '审核不通过';
		  this.config.status= {
			N:'禁止',
			S:'冻结',
			Y:"正常",
		  }
	  }),
      id
        ?
		Fetch2('/user/user/edit/' + id).then(r => {
            this.form = r.data.view
			
			var str = JSON.parse(this.form.id_card_pic);
			this.idCard.card1 = str.card1;
			this.idCard.card2 = str.card2;
			this.idCardList=[]
			this.idCardList.push(this.idCard.card1)
			this.idCardList.push(this.idCard.card2);
          })
        : null,
    ])
      .then(() => {
        this.loading = false
      })
      .catch(() => (this.loading = false))
  },
  methods: {
    onSubmit() {
      const id = this.$router.history.current.params.id
	  
	  if(this.form.is_auth=='F' && !this.form.authFailed){
	  		  Message.warning('请输入审核不通过原因')
	  		  return;
	  }
	  this.form.is_auth_name= this.is_auth[this.form.is_auth].yn;
	  
      this.loading = true
      if (id) {
        Fetch3('/user/user/edit/' + id, {
          id: this.$router.history.current.params.id,
          ...this.form,
          passwd: this.form.passwd ? this.form.passwd : undefined,
          pay_pwd: this.form.pay_pwd ? this.form.pay_pwd : undefined,
        })
          .then(r => {
            this.loading = false
            Message.success(r.msg)
            this.$router.replace('/member/list')
          })
          .catch(() => (this.loading = false))
      } else {
        Fetch('/user/user/create', {
          id: this.$router.history.current.params.id,
          ...this.form,
          passwd: this.form.passwd ? this.form.passwd : undefined,
          pay_pwd: this.form.pay_pwd ? this.form.pay_pwd : undefined,
        })
          .then(r => {
            this.loading = false
            Message.success(r.msg)
            this.$router.replace('/member/list')
          })
          .catch(() => (this.loading = false))
      }
    },
  },
}
</script>
